import { graphql, PageProps } from 'gatsby'
import { useCallback, useMemo } from 'react'
import { IGatsbyImageData } from 'gatsby-plugin-image'
import { Head, Category, PostList, Banner } from '@/components'
import { BASE_LINE, HOME_TITLE, INTERSECTION_OBSERVER_ROOT_MARGIN } from '@/constants'
import { toFit } from '@/utils/event-manager'
import useCategory from '@/hooks/useCategory'
import useIntersectionObserver from '@/hooks/useIntersectionObserver'
import useRenderedCount from '@/hooks/useRenderedCount'
import useScrollEvent from '@/hooks/useScrollEvent'
import { addClass, getDocumentHeight } from '@/utils/dom'
import { CategoryConst } from '@/types/Literal'

interface IndexPageDataProps {
  site: {
    siteMetadata: {
      title: string
      keywords: string[]
      configs: {
        countOfInitialPost: number
      }
    }
  }
  allMarkdownRemark: {
    edges: {
      node: {
        frontmatter: {
          date: string
          title: string
          category: string
          draft: boolean
          thumbnail?: {
            childImageSharp: {
              gatsbyImageData: IGatsbyImageData
            }
          }
        }
        excerpt: string
        fields: {
          slug: string
        }
      }
    }[]
  }
}

const IndexPage = ({ data }: PageProps<IndexPageDataProps>) => {
  const { siteMetadata } = data.site
  const { countOfInitialPost } = siteMetadata.configs
  const posts = data.allMarkdownRemark.edges
  const [count, countRef, increaseCount] = useRenderedCount()
  const [category, selectCategory] = useCategory()

  const getDistance = useCallback((currentPos: number) => {
    return getDocumentHeight() - currentPos
  }, [])

  useIntersectionObserver(
    (entries: IntersectionObserverEntry[]) => {
      return entries
        .filter(({ isIntersecting }) => isIntersecting)
        .forEach(({ target }) => addClass(target as HTMLElement, 'observed-shown'))
    },
    {
      rootMargin: INTERSECTION_OBSERVER_ROOT_MARGIN,
    },
  )

  useScrollEvent(() => {
    const currentPos = window.scrollY + window.innerHeight
    const isTriggerPos = () => getDistance(currentPos) < BASE_LINE
    const doesNeedMore = () => posts.length > countRef.current * countOfInitialPost

    return toFit(increaseCount, {
      dismissCondition: () => !isTriggerPos(),
      triggerCondition: () => isTriggerPos() && doesNeedMore(),
    })()
  })

  const refinedPosts = useMemo(
    () =>
      posts
        .filter(({ node }) => category === 'ALL' || node.frontmatter.category === category)
        .slice(0, count * countOfInitialPost)
        .map(({ node }) => ({
          id: node.frontmatter.title,
          title: node.frontmatter.title,
          thumbnail: node.frontmatter.thumbnail?.childImageSharp.gatsbyImageData,
          excerpt: node.excerpt,
          slug: node.fields.slug,
        })),
    [category, count, countOfInitialPost, posts],
  )

  return (
    <>
      <Head title={HOME_TITLE} keywords={siteMetadata.keywords} />
      <main>
        <Banner />
        <Category value={category} items={[...CategoryConst]} onClick={selectCategory} />
        <PostList posts={refinedPosts} />
      </main>
    </>
  )
}

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
        configs {
          countOfInitialPost
        }
      }
    }
    allMarkdownRemark(
      sort: { fields: [frontmatter___date], order: DESC }
      filter: { frontmatter: { category: { ne: null }, draft: { eq: false } } }
    ) {
      edges {
        node {
          excerpt(pruneLength: 280, truncate: true)
          fields {
            slug
          }
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            title
            thumbnail {
              childImageSharp {
                gatsbyImageData
              }
            }
            category
            draft
          }
        }
      }
    }
  }
`

export default IndexPage
